import React, {Component} from 'react';
import { Row, Table, Divider} from 'antd';
import { Collapse, Button} from 'reactstrap';
import Image from 'react-bootstrap/Image';

const columns = [
  {
    title: 'NAME',
    dataIndex: 'name',
    key: 'NAME',
    render: (text, record, index) => {
        if (record.name !== 'Total') {
            return <a href={"https://stats.nba.com/player/" + record.key}>{text}</a>;
        } else {
            return <h5><a href={"https://www.nba.com/teams/" + record.key}>{text}</a></h5>;
        }
    },
  },
  {
    title: 'MIN',
    dataIndex: 'min',
    key: 'MIN',
  },
  {
    title: 'FG',
    dataIndex: 'fg',
    key: 'FG',
  },
  {
    title: '3PT',
    dataIndex: 'fg3pt',
    key: 'FG3PT',
  },
  {
    title: 'FT',
    dataIndex: 'ft',
    key: 'FT',
  },
  {
    title: 'OREB',
    dataIndex: 'oreb',
    key: 'OREB',
  },

  {
    title: 'DREB',
    dataIndex: 'dreb',
    key: 'DREB',
  },
  {
    title: 'REB',
    dataIndex: 'reb',
    key: 'REB',
  },
  {
    title: 'AST',
    dataIndex: 'ast',
    key: 'AST',
  },
  {
    title: 'STL',
    dataIndex: 'stl',
    key: 'STL',
  },
  {
    title: 'BLK',
    dataIndex: 'blk',
    key: 'BLK',
  },
  {
    title: 'TO',
    dataIndex: 'to',
    key: 'TO',
  },
  {
    title: 'PF',
    dataIndex: 'pf',
    key: 'PF',
  },
  {
    title: '+/-',
    dataIndex: 'plusminus',
    key: 'PLUS_MINUS',
  },
  {
    title: 'PTS',
    dataIndex: 'pts',
    key: 'PTS',
  }
];

class Game extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
        collapse: false,
     };
  }

  toggle() {
    this.setState(state => ({ collapse: !state.collapse }));
  }

 render() {
    if (this.props.resultSets != null) {
        const awayTeam = {
            city: this.props.resultSets[1].rowSet[0][4],
            name: this.props.resultSets[1].rowSet[0][2],
            abb: this.props.resultSets[1].rowSet[0][3],
            score: this.props.resultSets[1].rowSet[0][23],
            players: []
        };

        const homeTeam = {
            city: this.props.resultSets[1].rowSet[1][4],
            name: this.props.resultSets[1].rowSet[1][2],
            abb: this.props.resultSets[1].rowSet[1][3],
            score: this.props.resultSets[1].rowSet[1][23],
            players: []
        };

        const players = this.props.resultSets[0].rowSet;
        for (var i = 0; i < players.length; i++) {
            const player = {
               key: this.props.resultSets[0].rowSet[i][4],
               name: this.props.resultSets[0].rowSet[i][5],
               min: this.props.resultSets[0].rowSet[i][8],
               fg: this.props.resultSets[0].rowSet[i][9] + "-" + this.props.resultSets[0].rowSet[i][10],
               fg3pt: this.props.resultSets[0].rowSet[i][12] + "-" + this.props.resultSets[0].rowSet[i][13],
               ft: this.props.resultSets[0].rowSet[i][15] + "-" + this.props.resultSets[0].rowSet[i][16],
               oreb: this.props.resultSets[0].rowSet[i][18],
               dreb: this.props.resultSets[0].rowSet[i][19],
               reb: this.props.resultSets[0].rowSet[i][20],
               ast: this.props.resultSets[0].rowSet[i][21],
               stl: this.props.resultSets[0].rowSet[i][22],
               blk: this.props.resultSets[0].rowSet[i][23],
               to: this.props.resultSets[0].rowSet[i][24],
               pf: this.props.resultSets[0].rowSet[i][25],
               plusminus: this.props.resultSets[0].rowSet[i][27],
               pts: this.props.resultSets[0].rowSet[i][26],
            };

            if (this.props.resultSets[0].rowSet[i][7].substring(0,2) !== "") {
                player.fg = null;
                player.ft = null;
                player.fg3pt = null;
                player.min = this.props.resultSets[0].rowSet[i][7].substring(0,3);
            }
            if (this.props.resultSets[0].rowSet[i][2] === awayTeam.abb) {
                awayTeam.players.push(player);
            } else if (this.props.resultSets[0].rowSet[i][2] === homeTeam.abb) {
                homeTeam.players.push(player);
            }
        }

         for (i = 0; i < 5; i++) {
              if (i === 0 || i === 1) {
                    homeTeam.players[i].name += " (F)";
                    awayTeam.players[i].name += " (F)";
                } else if (i === 2) {
                    homeTeam.players[i].name += " (C)";
                    awayTeam.players[i].name += " (C)";
                } else if (i === 3 || i === 4) {
                    homeTeam.players[i].name += " (G)";
                    awayTeam.players[i].name += " (G)";
                }
         }

         const awayTotal = {
            name: "Total",
            key: this.props.resultSets[1].rowSet[0][2].toLowerCase(),
            fg: this.props.resultSets[1].rowSet[0][6] + "-" + this.props.resultSets[1].rowSet[0][7],
            fg3pt: this.props.resultSets[1].rowSet[0][9] + "-" + this.props.resultSets[1].rowSet[0][10],
            ft: this.props.resultSets[1].rowSet[0][12] + "-" + this.props.resultSets[1].rowSet[0][13],
            oreb: this.props.resultSets[1].rowSet[0][15],
            dreb: this.props.resultSets[1].rowSet[0][16],
            reb: this.props.resultSets[1].rowSet[0][17],
            ast: this.props.resultSets[1].rowSet[0][18],
            stl: this.props.resultSets[1].rowSet[0][19],
            blk: this.props.resultSets[1].rowSet[0][20],
            to: this.props.resultSets[1].rowSet[0][22],
            pf: this.props.resultSets[1].rowSet[0][22],
            pts: this.props.resultSets[1].rowSet[0][23],
            plusminus: this.props.resultSets[1].rowSet[0][24],
         };

         const homeTotal = {
            name: "Total",
            key: this.props.resultSets[1].rowSet[1][2].toLowerCase(),
            fg: this.props.resultSets[1].rowSet[1][6] + "-" + this.props.resultSets[1].rowSet[1][7],
            fg3pt: this.props.resultSets[1].rowSet[1][9] + "-" + this.props.resultSets[1].rowSet[1][10],
            ft: this.props.resultSets[1].rowSet[1][12] + "-" + this.props.resultSets[1].rowSet[1][13],
            oreb: this.props.resultSets[1].rowSet[1][15],
            dreb: this.props.resultSets[1].rowSet[1][16],
            reb: this.props.resultSets[1].rowSet[1][17],
            ast: this.props.resultSets[1].rowSet[1][18],
            stl: this.props.resultSets[1].rowSet[1][19],
            blk: this.props.resultSets[1].rowSet[1][20],
            to: this.props.resultSets[1].rowSet[1][22],
            pf: this.props.resultSets[1].rowSet[1][22],
            pts: this.props.resultSets[1].rowSet[1][23],
            plusminus: this.props.resultSets[1].rowSet[1][24],
         };
         awayTeam.players.push(awayTotal);
         homeTeam.players.push(homeTotal);

    const logo_url_1 = 'https://github.com/vandyliu/boxscorequick/blob/gh-pages/images/logos/';
    const logo_url_2 = '.png?raw=true';
    return (
        <div>
            <Row>
                 <h2><a href={"https://stats.nba.com/game/" + this.props.GameID}> <Image src ={logo_url_1 + awayTeam.abb.toLowerCase() + logo_url_2}/> {awayTeam.city} {awayTeam.name} {awayTeam.score}  vs {homeTeam.score} {homeTeam.city} {homeTeam.name} <Image src ={logo_url_1 + homeTeam.abb.toLowerCase() + logo_url_2}/> </a><Button className = "float-right" outline color="secondary" onClick={this.toggle}> Toggle </Button> </h2>
            </Row>
            <Collapse isOpen={this.state.collapse}>
                <Row type="flex" justify="start">
                    <h3><Image src ={logo_url_1 + awayTeam.abb.toLowerCase() + logo_url_2}/>&nbsp; {awayTeam.name} </h3>
                </Row>
                <Row>
                    <Table dataSource={awayTeam.players} columns={columns} size="small" pagination={false} rowKey = {player => player.key} />
                </Row>

                <Divider dashed/>

                <Row type="flex" justify="start">
                    <h3><Image src ={logo_url_1 + homeTeam.abb.toLowerCase() + logo_url_2}/>&nbsp; {homeTeam.name} </h3>
                </Row>
                <Row>
                    <Table dataSource={homeTeam.players} columns={columns} size="small" pagination={false} />
                </Row>
            </Collapse>
        </div>
    );
    } else {
        return (
            <div> <h1> An error has occurred </h1> </div>
        );
    }
  }
}

export default Game;