import React from 'react';

class About extends React.Component {
    render() {
        return (
            <div align="left" className='about'>
                <h3>About</h3>
                <p>BoxScoreQ is a website to keep you updated on NBA games and their box scores quickly. Whenever I come home from work or school, I open up and see what NBA games I should tune in to. However, I would always want to look at the box score first to see if there is a particular game that might interest me like a game where Derrick Rose currently has 41 points or a game where Klay Thompson is 6-7 from 3 point range. There wasn’t a quick place to find all the box scores in one place, which is why I made BoxScoreQ.</p>
                Find out more about this project at <a href="https://vandyliu.github.io">my blog</a>

                <h3>FAQ</h3>
                <ul>
                    <li>
                        <b>Why is nothing showing up? </b>
                        The backend is not yet deployed. It is finished but I just need to do some final testing :)
                    </li>
                    <li>
                        <b>How far back can we check? </b>
                        You can check as far back as the 2009-10 season but all dates before the 2017-18 season are not cached in the database so they may take a longer time to load.
                    </li>
                    <li>
                        <b>Why do some dates load so slowly? </b>
                        The NBA API starts blocking requests after a few are made from the
                        same IP address so I have to request from proxies until the website
                        has all the information. Often these proxies are also blocked or don't
                        work so it can take a while. Once a date has been added, it is added to
                        a database, which does not block or need a proxy to access so the second
                        time you look up a date, it will be much faster.
                    </li>
                    <li>
                        <b>How often are the box scores for the current day updated? </b>
                        They are updated every 5 minutes.
                    </li>
                </ul>
             </div>
        )
    }
}

export default About;