import React from 'react';
import Game from '../components/Game';
import LoadingBar from '../components/LoadingBar';
import { Divider, DatePicker } from 'antd';
import moment from 'moment';

class GameList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {amount_of_games: -1, games: [], date: moment().format('MMDDYYYY'), loading: 1};
    }

    componentDidMount() {
        this.gamesList();
    }

    componentDidUpdate(prevProps, prevState) {
       if (this.state.date !== prevState.date) {
            this.setState({loading: 1});
            this.gamesList();
       }
    }

    onChange(date, dateString) {
        const formattedDate = moment(dateString, "YYYY-MM-DD").format('MMDDYYYY');
        this.setState({date: formattedDate});
    }

    gamesList() {
        fetch('https://boxscorequick.pythonanywhere.com/date/' + this.state.date)
            .then(response => response.json())
            .then(data => this.setState({games: data.games, amount_of_games: data.amount_of_games, loading: 0}))
            .catch(function(err) {
                console.log(err);
            });
    }

    disabledDate(current) {
        const earliest = moment('2009-09-30', 'YYYY-MM-DD').endOf('day');
        return earliest > current || current > moment().endOf('day');
    }

    render() {
        const loading = this.state.loading;
        const amount_of_games = this.state.amount_of_games;
        if (loading === 1) {
            return (
                <div>
                    <DatePicker disabledDate={this.disabledDate} onChange = {this.onChange.bind(this)} defaultValue={moment()}/>
                    <Divider/>
                    <LoadingBar loading={loading} />
                </div>
            );
        }
        if (amount_of_games === 0) {
            return (
                <div>
                    <DatePicker disabledDate={this.disabledDate} onChange = {this.onChange.bind(this)} defaultValue={moment()}/>
                    <Divider/>
                    { <h1> No games to display </h1> }
                </div>
            );
        } else {
            const loading = this.state.loading;
            const games = this.state.games;
            const gamesList = games.map(function(game) {
                return <div key={game.GameID}> <Game {...game} /> <Divider/> </div>;
            });
            return (
                <div>
                    <DatePicker disabledDate={this.disabledDate} onChange = {this.onChange.bind(this)} defaultValue={moment()}/>
                    <Divider/>
                    { gamesList }
                </div>
            );
        }
    }
}

export default GameList;